/* ----------------Text Fonts ------------------- */
body {
  font-size: 14px;
  line-height: 25px;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
  color: #838383;
  font-style: normal;
  overflow-x: hidden;
}
h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  color: #1565C0;
  line-height: 1.3;
}
h1, .h1 {
  font-size: 48px;
  margin-bottom: 25px;
}
h2, .h2 {
  font-size: 42px;
  margin-bottom: 25px;
}
h3, .h3 {
  font-size: 36px;
  margin-bottom: 25px;
}
h4, .h4 {
  font-size: 24px;
  margin-bottom: 15px;
}
h5, .h5 {
  font-size: 18px;
  margin-bottom: 15px;
}
h6, .h6 {
  font-size: 14px;
  margin-bottom: 10px;
}
p {
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  margin: 0 0 15px;
  line-height: 24px;
  font-weight: 400;
  color: #838383;
}
p:last-child {
  margin-bottom: 0;
}
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  font-size: 13px;
  font-weight: 400;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
a:hover,
a:focus,
a:active {
  text-decoration: none;
  outline: none;
  color: #838383;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
ul.nosyely_list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.section_title {
  position: relative;
  margin-bottom: 25px;
}
.section_title .title {
  text-transform: uppercase;
  padding: 10px 0;
  font-size: 36px;
}
.section_title .title:before {
  width: 30px;
  content: " ";
  position: absolute;
  border-top: solid 3px #2196F3;
  left: 0;
  top: 0;
}
.countdown {
  text-align: center;
}
.countdown div {
  margin: 20px;
  display: inline-block;
  padding: 25px;
  min-width: 120px;
  background-color: #2196F3;
  color: #ffffff;
  font-size: 48px;
  font-weight: 600;
  text-align: center;
  line-height: 1.2;
}
.countdown div span {
  display: block;
  font-size: 14px;
  font-weight: 400;
}
.bg-img {
  border-radius: 4px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
}
.bg-img .overlay_dark {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.brand_item {
  opacity: 0.6;
  -webkit-transition: all ease-in-out 0.4s;
  transition: all ease-in-out 0.4s;
}
.brand_item img {
  display: block;
  max-width: 200px;
  margin: 0 auto;
}
.brand_item:hover {
  opacity: 1;
  -webkit-transition: all ease-in-out 0.4s;
  transition: all ease-in-out 0.4s;
}
.icon_list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.icon_list li {
  display: inline-block;
  width: 100%;
  position: relative;
  padding-left: 50px;
  min-height: 50px;
  margin-bottom: 7px;
}
.icon_list li i {
  background-color: #2196F3;
  display: block;
  border-radius: 50px;
  padding: 7px 0;
  font-size: 24px;
  color: #ffffff;
  width: 40px;
  height: 40px;
  text-align: center;
  position: absolute;
  left: 0;
}
.icon_list li span {
  color: #333333;
  padding-top: 6px;
  display: inline-block;
}
blockquote {
  padding: 75px 25px 25px;
  font-size: 20px;
  text-align: center;
  margin: 25px 50px;
}
blockquote:before {
